.Portfolio{
  position: fixed;
  left: 5%;
  right: 5%;
  top: 10%;
  bottom: 10%;
}

.Portfolio .Scroll{
  position: absolute;
  overflow-y: scroll;
  top: 45px;
  bottom: 0;
  left: -1rem;
  right: -1rem;
}

.Portfolio span{
  color: rgb(214, 214, 214);
  font-family: 'VT323', monospace;
  font-size: 18px;
}

.Portfolio-Back::after{
  content: "";
  border-radius: 7px;
  opacity: 0.35;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: -1; 
  background-size: cover;
  transition: opacity 200ms ease-in-out, border-radius 200ms ease-in-out;
}

.Portfolio-Back:hover::after{
  border-radius: 15px;
  opacity: 0.6;
}

.Scroll ul {
  display: flex;
  flex-flow: row wrap;
  list-style: none;
  padding: 0 0 0 0;
  margin: 0;
}
.Scroll ul li {
  display: flex;
  width: calc(100% / 4 - 2rem);
  min-height: 200px;
  margin: 1rem;
}

.Scroll ul li div {
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  display: flex;
  flex: 1 1;
}

.Scroll ul li a {
  display: flex;
  flex: 1 1;
  flex-flow: column;
  padding: 2rem;
  align-items: center;
  justify-content: center;

  text-align: center;
  text-decoration: none;
  color: white;
  -webkit-transform: translateZ(30px);
          transform: translateZ(30px);
  font-family: 'VT323', monospace;
  font-size: 25px;
  transition: opacity 150ms ease-out;
  opacity: 0.6;
}
.Scroll ul li a:hover {
  opacity: 1;
}

